.mask {
  width: 100%;
  height: 100vh;
  position: relative;
}
.intro-img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}
.mask::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.8;
}
.hero {
  height: 100%;
  width: 100%;
}
.hero p span {
  color: aqua;
}
.hero .profile > img{
  width:50%;
  height: 40vh;
  border-radius: 50%;
  background-position: center;
}
.hero .profile > img:hover{
  width:90%;
  height: 60vh;
}
.hero > .content {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}
.hero > .content > h1 {
  font-size: 4rem;
  padding: 0.6rem 0 1.5rem;
}

.hero .content p {
  font-size: 1.4rem;
  font-weight: 200;
  text-transform: uppercase;
}

.content .btn {
  margin: 1rem 0.2rem;
}

@media screen and (max-width: 900px) {
  .intro-img {
    width: 100%;
    height: 100%;
    background-position: center;

  }
  .hero > .content > h1 {
    font-size: 3rem;
  }
  .hero .content p {
    font-size: 1.4rem
  }
  .hero .profile > img{
    width:50%;
    height: 20vh;
    border-radius: 60%;
    background-position: center;
  }
  .hero .profile > img:hover{
    width:60%;
    height:30vh;
  }
  .hero p {
    width:240px;
    margin: auto;
 }
 .hero p span {
  color: aqua;
}
}
